<template>
	<div id="address-location">
		<public-header title="新增地址"></public-header>
		<transition name="loading">
			<iloading v-show="showLoading"></iloading>
		</transition>
		<div class="content">
			<van-search placeholder="请输入您的地址" v-model="key"/>
			<template v-if="!key">
				<div class="map-content">
					<div id="allmap1"></div>
					<div class="center-marker">
						<img src="static/img/marker_red.png" alt="">
					</div>
				</div>
				<div class="location-list" v-if="addressPois.length > 0">
					<div class="list-item flex-lr van-hairline--bottom " :class="{active: index == 0}" v-for="(item, index) in addressPois" @click="onSelectAddress(item)" :key="index">
						<i class="icon icon-locationfill"></i>
						<div class="item-inner">
							<div class="name ellipsis">{{item.address}}</div>
							<div class="address ellipsis">{{item.name}}</div>
						</div>
					</div>
					<div class="padding-15-r padding-15-tb text-center font-14" >
						<span class="c-danger" @click="onToggleHandAddDialogStatus">未找到地址？点我手动添加</span>
					</div>
				</div>
			</template>
			<van-cell-group class="search-list" v-else>
				<van-cell v-for="(item,index) in addressSearch"  :title="item.address"  :label="item.name" @click="onSelectAddress(item)" :key="index"/>
				<div class="padding-15-tb text-center font-14">
					<span class="c-danger" @click="onToggleHandAddDialogStatus">未找到地址？点我手动添加</span>
				</div>
			</van-cell-group>
		</div>
		<van-dialog
				v-model="handAddDialogStatus"
				title="添加收货地址"
				show-cancel-button
				@confirm="onConfirmAddress"
		>
			<div class="padding-15">
				<input v-model="handAddress" type="text" class="w-100" placeholder="请输入地址">
			</div>
		</van-dialog>
	</div>
</template>

<script>
	import {Amap} from '@/config/AMap'
	import {GMap} from '@/config/GMap'
	import {getData} from '@/controller/funcCommon'
	import {mapState, mapMutations} from 'vuex'
	import PublicHeader from '@/components/header'
	import axios from 'axios'
	export default {
		data() {
			return {
				query: {},
				showLoading: true,
				key: '',
				mapConfig: {
					center: {}
				},
				map: {
					lat: '',
					lng: ''
				},
				addressPois: [],
				addressSearch: [],
				handAddDialogStatus: false,
				handAddress: '',
				MapType: 'gaode',
			}
		},
		components: {
			PublicHeader,
		},
		computed: {
			...mapState(['editAddress', 'erranderExtra', 'erranderStoreExtra']),
		},

		watch: {
			key() {
				let key= this.key;
				this.addressSearch = [];
				var url = 'system/common/map/suggestion';
				if(this.MapType == 'google') {
					url = 'system/common/map/suggestion_google';
				}
				var plugin = 'takeout';
				if(this.channel == 'errander') {
					plugin = 'errander';
				}
				if(key) {
					this.util.request({
						url: url,
						data: {
							key: key,
							plugin: plugin
						}
					}).then((res) => {
						let result = res.data.message;
						if(!result.errno && result.message && result.message.length > 0) {
							this.addressSearch = [...this.addressSearch, ...result.message];
						}
					})
				}
			}
		},
		methods: {
			...mapMutations(['replaceAddress', 'setState', 'replaceState']),

			onToggleHandAddDialogStatus() {
				var that = this;
				that.handAddDialogStatus = !that.handAddDialogStatus;
			},

			onConfirmAddress() {
				var that = this;
				if(!that.handAddress) {
					that.util.$toast('请输入要添加的坐标名称')
					return;
				}
				this.util.request({
					url: 'wmall/member/address/handAdd',
					data: {
						address: that.handAddress
					},
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message);
						return;
					}
					result = result.message;
					var address = result.address;
					if(address && address.location_x && address.location_y) {
						that.onSelectAddress(address);
					}
				});
			},

			onLoad() {
				var params = {
					sid: this.sid,
					erranderId: this.erranderId
				};
				if(this.erranderExtra && this.erranderExtra.agentid) {
					params['agentid'] = this.erranderExtra.agentid;
				}
				this.util.request({
					url: 'wmall/member/address/location',
					data: params,
				}).then((res) => {
					this.showLoading = false;
					let result = res.data.message;
					this.mapConfig = result.message.map;
					this.map.lat = result.message.map.center.location_x;
					this.map.lng = result.message.map.center.location_y;
					this.map.keywords = result.message.map.keywords;
					this.MapType = this.util.getStorage('MapType');
					this.loadmap();
					if(!this.key) {
						this.onGetAddressPois(this.map.lat, this.map.lng);
					}
				});
			},

			onGetAddressPois(lat, lng) {
				var that = this;
				if(that.MapType == 'gaode') {
					var params = {
						types: '商务住宅|科教文化服务|地名地址信息|医疗保健服务|政府机构及社会团体|住宿服务|公司企业|道路附属设施|购物服务|生活服务|体育休闲服务|交通设施服务',
						location: lng + ',' + lat,
					};
					if(this.map.keywords) {
						params['keywords'] = this.map.keywords;
					}
					var url = 'https://restapi.amap.com/v3/place/around?key=37bb6a3b1656ba7d7dc8946e7e26f39b&radius=1000&offset=25&page=1&extensions=all&output=json&json=' +  this.util.toQueryString(params);
					axios.get(url).then((result) => {
						var result = result.data;
						if(result.info == "OK"){
							var re = [];
							for(var i in result.pois){
								var location = result.pois[i].location.split(',');
								var poi = {
									name: result.pois[i].address,
									address: result.pois[i].name,
									lng: location[0],
									lat: location[1],
									location_x: location[1],
									location_y: location[0],
								};
								re.push(poi);
							}
							this.addressPois = re;
						}
					});
				} else {
					getData({
						vue: that,
						url: 'system/common/map/place_around_google',
						data: {
							lat: lat,
							lng: lng,
						},
						success: (res) => {
							that.addressPois = [...res.place_around];
						},
						fail: (res) => {
							that.util.$toast(res.message);
						}
					})
				}

			},

			onSelectAddress(item) {
				var that = this;
				if(that.mapConfig.address_name_mode == 1) {
					item.address = item.address + '(' + item.name + ')';
				}
				if(that.channel == 'erranderStoreInput') {
					var address_input = that.erranderStoreExtra.address_input;
					address_input.address = item.address;
					address_input.location_x = item.location_x;
					address_input.location_y = item.location_y;
					that.setState({type: 'erranderStoreExtra', key: 'address_input', val: address_input});
					that.$router.replace(that.util.getUrl({path: '/plugin/pages/paotui/store/order/diy', query: that.util.extend(that.query)}));
				} else {
					that.replaceAddress(item);
					if(that.editAddress) {
						that.$router.replace(that.util.getUrl({path: '/pages/member/addressPost', query: that.util.extend(that.query, {from: 'location'})}));
					}
				}
			},

			loadmap(){
				var that = this;
				if(that.MapType == 'gaode') {
					Amap().then(function(AMap){
						window.map = that.map = new AMap.Map('allmap1', {
							resizeEnable: true,
							center: [that.mapConfig.center.location_y, that.mapConfig.center.location_x],
							zoom: 17
						});

						AMap.event.addListener(that.map, "dragend", function(){
							var center = that.map.getCenter();
							that.onGetAddressPois(center.lat, center.lng);
						});

						that.util.getLocation({
							successLocation: function(location) {
								that.map.panTo([location.lng, location.lat]);
								that.onGetAddressPois(location.lat, location.lng);
							},
							fail: function(res) {
							}
						});
					});
				} else {
					GMap().then(function(google) {
						window.map = that.map = new google.maps.Map(document.getElementById('allmap1'), {
							center: {
								lat: parseFloat(that.mapConfig.center.location_x), 
								lng: parseFloat(that.mapConfig.center.location_y)
							},
							zoom: 17,
							disableDefaultUI: true  //禁用默认UI
						});

						//地图拖拽中心点发生变化
						that.map.addListener('center_changed', function() {
							window.setTimeout(function() {
								var center = that.map.getCenter();
								center = JSON.parse(JSON.stringify(center))
								that.onGetAddressPois(center.lat, center.lng)
							}, 500);
						});

						//获取定位
						that.util.getLocation({
							successLocation: function(location) {
								that.map.panTo({lat: location.lat, lng: location.lng});
								that.onGetAddressPois(location.lat, location.lng);
							},
							fail: function(res) {
							},
							mapType: 'google'
						});

					});
				}
			},

		},
		created() {
			this.query = this.util.parseQuery(this.$route.query);
			if(this.query) {
				this.id = this.query.id;
				this.sid = this.query.sid;
				this.channel = this.query.channel;
				this.input = this.query.input;
				this.erranderId = this.query.erranderId;
				this.orderId = this.query.orderId;
				this.key = this.query.key;
			}
		},
		mounted(){
			this.onLoad();
		},
	}
</script>

<style>
	#address-location .map-content{
		width: 100%;
		height: 200px;
		position: relative;
	}
	#address-location .map-content #allmap1{
		width: 100%;
		height: 100%;
	}
	#address-location .map-content .center-marker{
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -9.5px;
		margin-top: -25px;
	}
	#address-location .content{
		position: absolute;
		left: 0;
		bottom: 0;
		right: 0;
	}
	#address-location .content .location-list{
		overflow-y: auto;
	}
	#address-location .content .location-list::-webkit-scrollbar {
		display: none;
	}
	#address-location .van-search .van-icon-clear{
		display: none;
	}
	#address-location .content .search-list{
		position: absolute;
		top: 40px;
		right: 0;
		bottom: 0;
		left: 0;
		overflow-y: auto;
	}
	#address-location .content .search-list::-webkit-scrollbar {
		display: none;
	}
	#address-location .content .location-list{
		background-color: #fff;
		padding-left: 15px;
	}
	#address-location .content .location-list .list-item{
		padding: 10px 15px 10px 0;
	}
	#address-location .content .location-list .list-item .icon{
		align-self: flex-start;
		font-size: 16px;
		color: #FF2D4B;
		margin-right: 2px;
		opacity: 0;
	}
	#address-location .content .location-list .list-item .item-inner{
		width: 100%;
	}
	#address-location .content .location-list .list-item .name{
		font-size: 14px;
	}
	#address-location .content .location-list .list-item .address{
		font-size: 12px;
		color: #999;
		margin-top: 10px;
	}
	#address-location .content .location-list .list-item.active .name{
		color: #FF2D4B;
	}
	#address-location .content .location-list .list-item.active .icon{
		opacity: 1;
	}
	#address-location .content .van-button.van-button--plain.van-button--danger {
		color: #ff2d4b;
		border-color: #ff2d4b;
	}
	#address-location .van-button.van-dialog__confirm{
		color: #ff2d4b;
	}
</style>



